import React from 'react';
import { social, links } from './data';
import './NavBar.css';
import {Link} from "react-scroll";

const NavBar = (props) => {

  

    return (
        <nav>
        <div className='sidebar-header'>
            <span className="logo-part1">Boom</span>
            <span className="logo-part2">.io</span>
        </div>
        <ul className='links'>
        {links.map((link) => {
          const { id, url, text } = link;
          return (
            <li key={id} class={id} id={`link-${id}`}>
              
              <Link
                    activeClass="active"
                    to={url}
                    spy={true}
                    smooth={true}
                    offset={-100}
                    duration={500}
                > 
                    {text}
                </Link>
            </li>
           
          );
        })}
      </ul>
      <ul className='social-icons'>
        {social.map((link) => {
          const { id, url, icon } = link;
          return (
            <span className='icon-holder'>
            <li key={id}>
              <a href={url}>{icon}</a>
            </li>
            </span>
          );
        })}
      </ul>
      </nav>

        
    );
};
export default NavBar;